.ov-slider {
  position: relative; }
  .ov-slider .ov-slider-button {
    position: absolute;
    top: 130px;
    z-index: 5;
    padding: 10px;
    background-color: rgba(0, 0, 0, 0.25);
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none; }
    .ov-slider .ov-slider-button:hover {
      color: #fff; }
    .ov-slider .ov-slider-button.ov-prev-button {
      left: 0; }
    .ov-slider .ov-slider-button.ov-next-button {
      right: 0; }
